export enum MediaTypes {
  VIDEO = 'video',
  IMAGE = 'image',
}

export interface Point {
  x: number;
  y: number;
}
export interface ImageMetaDataType extends BaseMetaDataType {
  type: MediaTypes.IMAGE;
  focalPoint?: Point;
}

export interface VideoMetaDataType extends BaseMetaDataType {
  type: MediaTypes.VIDEO;
  posters?: {
    url: string;
    width: number;
    height: number;
  }[];
}

export interface ResizedMediaItem {
  item: ItemContentData;
  dto: {
    imageToken: string;
  };
}

export type ResizeMethod = 'fit' | 'fill' | 'crop';

export interface ResizedImageSource {
  dpr: string;
  forPrinting?: boolean;
  type: string;
  url: string;
}

export interface ResizeMediaParams {
  item: ResizedMediaItem;
  originalUrl: string | [ResizedImageSource];
  resizeMethod: ResizeMethod;
  requiredWidth: number;
  requiredHeight: number;
  focalPoint?: Point;
  createMultiple?: boolean;
  imageToken?: string;
  staticMediaUrls?: object;
  shouldUseOptimizedImageFormat?: boolean;
  isPrerenderMode?: boolean;
}

export interface GallerySelectors {
  root?: string;
  container?: string;
  item?: string;
  media?: string;
  image?: string;
  details?: string;
  info?: string;
  title?: string;
  description?: string;
  counter?: string;
  content?: string;
  overlay?: string;
  scroller?: string;
}

