export interface ItemContentData<MetaDataType = BaseMetaDataType> {
  id: string;
  index: string;
  mediaUrl: string | [ResizedImageSource];
  uri?: string;
  metaData: MetaDataType;
  type: MediaTypes.IMAGE | MediaTypes.VIDEO;
}

export interface BaseMetaDataType {
  width: number;
  height: number;
  title: string;
  description: string;
  alt: string;
  link: {
    url: string;
    target: string;
  };
}

export enum GapUnits {
  px = 'px',
  vw = 'vw',
  vh = 'vh',
}
